strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  @include os-reg;
  display: block;
  font-size: 40px;
  @include css-lock(24, 40, 320, 1399, font-size);
  line-height: 1;
  color: $black-light;
  &--h2 {
    @include css-lock(21, 35, 320, 1399, font-size); }
  &--h4 {
    @include css-lock(16, 21, 320, 1399, font-size); }
  &--h5 {
    @include css-lock(14, 16, 767, 1399, font-size); }
  &--h-bold {
    @include bold; }
  &--bold {
    @include os-bold; }

  &--lg {
    @include css-lock(25, 60, 320, 1399, font-size); }
  &--white {
    color: $white; }
  &--accent {
    color: $accent; }
  &--shadow {
    background-color: $accent;
    color: transparent;
    background-clip: text;
    text-shadow: 0 0 50px $accent, 0px 0px 10px rgba($white, .5); } }

.title-under {
  position: relative;
  padding-bottom: 0.4em;
  @include before {
    @include css-lock(100, 270, 320, 1399, width);
    background-color: $accent;
    height: 5px;
    bottom: 0;
    left: 1em;
    transform: skew(-70deg);
    position: absolute; }
  &--sm {
    @include before {
      width: 65px !important;
      left: 0;
      transform: skew(-30deg); } }
  &--inherit {
    @include before {
      background-color: currentColor; } }
  &--center {
    @include before {
      left: 50%;
      transform: translate(-50%, 0) skew(-70deg); } } }

.text {
  &--gray {
    color: $gray; } }
