.blog-articles {
  &__btn {
    text-align: center; } }

.blog-article {
  $this: &;
  @include flex(flex-start, normal, column);
  height: calc(100% - 30px);
  margin-bottom: 30px;
  @include sm {
    margin-bottom: 15px;
    height: auto; }
  @include hover {
    #{$this}__text, #{$this}__title {
      text-decoration: underline; } }

  &__img {
    @include cover;
    padding-top: 55%;
    display: block; }
  &__content {
    background-color: $white;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.13);
    padding: 20px 20px 35px;
    flex-grow: 1;
    @include flex(flex-start, normal, column);
    @include md {
      padding: 15px; } }
  &__title {
    margin-bottom: 25px;
    display: block;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 20px;
    display: block;
    flex-grow: 1;
    @include md {
      margin-bottom: 10px; } }
  &__meta {
    margin-bottom: 15px; } }

.blog-page {
  @include clr;
  h1, h2, h3, p, article, img, ul, ol, figure, blockquote {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  blockquote {
    margin-top: 60px;
    margin-bottom: 60px;
    @include lg {
      margin-top: 50px;
      margin-bottom: 50px; }
    @include md {
      margin-top: 40px;
      margin-bottom: 40px; }
    @include sm {
      margin-top: 30px;
      margin-bottom: 30px; }
    @include xs {
      margin-top: 15px;
      margin-bottom: 15px; } }


  h2 {
    @extend .title;
    @extend .title--h4;
    @extend .title--h-bold;
    @extend .title-under;
    @extend .title-under--sm;
    width: 50%;
    padding-right: 30px;
    @include xs {
      width: auto;
      padding-right: 0; } }
  img {
    float: right;
    width: 50%;
    margin-left: 30px;
    @include xs {
      width: 100%;
      float: none;
      margin-left: 0; } } }
