.types {}

.type-item {
  $this: &;
  color: $white;
  @include cover;
  position: relative;
  padding: 15% 25px 0;
  @include flex(flex-end, normal, column);
  margin-bottom: 30px;
  height: calc(100% - 30px);
  overflow: hidden;
  &:hover {
    #{$this}__content {
      transform: translate(0, 0); } }
  @include lg {
    padding-top: 90px; }
  @include md {
    padding-left: 20px;
    padding-right: 20px; }
  @include sm {
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 15px;
    height: auto;
    padding-top: 70px; }
  &__price {
    position: absolute;
    top: 35px;
    left: 25px;
    @include md {
      top: 20px;
      left: 20px; }
    @include sm {
      top: 15px;
      left: 15px; } }
  &__title {
    margin-bottom: 15px; }
  &__text {
    margin-bottom: 15px; }
  &__content {
    margin-bottom: 50px;
    transform: translate(0, 100px);
    @include tr(transform .3s);
    @include lg {
      margin-bottom: 40px;
      transform: translate(0, 90px); }
    @include md {
      margin-bottom: 30px;
      transform: translate(0, 80px); }
    @include sm {
      margin-bottom: 15px;
      transform: translate(0, 65px); } }
  &__btn {} }



