.contact {
  $this: &;
  @include os-reg;
  color: $black-light;
  position: relative;
  @include notlast {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 5px; } }
  &__icon {
    // position: absolute
    // top: 50%
    // left: 0
    // transform: translate(0, -50%)
    .icon {
      font-size: 25px; } }
  &__content {
    text-decoration: underline; }

  &--static {
    @include notlast {
      margin-bottom: 10px; }
    #{$this}__content {
      text-decoration: none; } } }


