.section {
  $this: &;
  position: relative;
  .word {
    left: percentage(3/12);
    top: 0; }
  &__title {
    position: relative;
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__title-mb-lg {
    margin-bottom: 65px;
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; } }
  &__top {
    @include flex(space-between,center);
    margin-bottom: 40px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      display: block;
      .section__title {
        margin-bottom: 10px; } }
    #{$this}__title {
      margin-bottom: 0; } }
  &__subttl {
    margin-bottom: 35px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__items {
    margin-top: 30px;
    margin-bottom: 30px; }
  &__contacts {
    @include xs {
      margin-bottom: 15px; } }

  &--white {
    color: $white; }
  &--bg {
    @include cover; } }

.s-about {
  background-size: 25%;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  @include md {
    background-size: 40%; }
  @include xs {
    background-size: 70%; }
  .word {
    @include css-lock(30, 100, 320, 1499, top); } }

.s-call {
  &__left {
    @include sm {
      margin-bottom: 15px; } } }

.s-stext {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  &__img {
    text-align: center;
    @include xs {
      margin-top: 25px; } } }

.s-order {
  margin-top: 200px;
  background-position: 30% 50%;
  @include md {
    margin-top: 0; }
  @include xs {
    background-position: 100% 50%; }
  &__left {
    height: 100%;
    @include flex(center, flex-end, column); }
  &__title {
    // max-width: 520px
    max-width: percentage(6/12);
    @include xs {
      max-width: none;
      margin-bottom: 25px; } } }




