.features {}

.feature {
  text-align: center;
  margin-bottom: 75px;
  @include lg {
    margin-bottom: 50px; }
  @include md {
    margin-bottom: 30px; }
  @include sm {
    margin-bottom: 15px; }
  &__title {
    margin-bottom: 10px;
    @include md {
      margin-bottom: 5px; } }
  &__icon {
    color: $accent;
    margin-bottom: 15px;
    width: 85px;
    height: 42px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    .icon {
      font-size: 35px; }
    .icon-trophy {
      @include vcenter;
      fill: $gray-light; } }
  &__text {
    @include lg {
      br {
        display: none; } } } }
