.projects {
  @extend .row;
  @include xs {
    margin-left: 0;
    margin-right: 0; }
  .project-link {
    margin-bottom: 30px;
    width: calc(25% - 30px);
    margin-left: 15px;
    margin-right: 15px;
    // padding-top: 35%
    @include md {
      width: calc(33.33333% - 30px);
 }      // padding-top: 45%
    @include sm {
      width: calc(50% - 30px);
 }      // padding-top: 55%
    @include xs {
      width: 100%;
      margin-left: 0;
      margin-right: 0; }
    @include xss {
 } } }      // padding-top: 100%


.project-link {
  // +flex(flex-end, normal, column)
  display: block;
  @include cover;
  color: $white;
  position: relative;
  &__content {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    padding: 20px;
    @include sm {
      padding: 10px; } }
  &__inner {
    display: block;
    @include cover;
    padding-top: 145%;
    @include md {
      padding-top: 45%; }
    @include sm {
      padding-top: 55%; }
    @include xss {
      padding-top: 100%; }
    @include sm {
      padding-top: 120%; }
    @include xs {
      padding-top: 80%; }
    @include xss {
      padding-top: 90%; } }
  &__title {
    display: block;
    @include bold;
    margin-bottom: 10px;
    @include sm {
      margin-bottom: 5px; } }
  &__subttl {
    color: $white-dark;
    display: block;
    font-size: $fz-sm; } }
