.footer {
  background-color: $white-dark;
  &__inner {
    padding-top: 70px;
    padding-bottom: 50px;
    @include xl {
      padding-top: 50px;
      padding-bottom: 50px; }
    @include lg {
      padding-top: 40px;
      padding-bottom: 40px; }
    @include md {
      padding-top: 30px;
      padding-bottom: 30px; }
    @include sm {
      padding-top: 20px;
      padding-bottom: 20px; } }
  &__logo {
    margin-bottom: 20px;
    @include lg {
      margin-bottom: 10px; } }
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__copy {
    font-size: 13px; }
  &__auth {
    margin-top: 40px;
    text-align: center;
    font-size: 13px;
    @include xl {
      margin-top: 30px; }
    @include lg {
      margin-top: 20px; }
    @include md {
      margin-top: 10px; }
    @include sm {
      margin-top: 0; }
    a {
      color: $blue; } }
  &__form {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__block {
    @include sm {
      margin-bottom: 15px; } }
  &__social {
    @include flex(flex-start, normal, row, wrap);
    margin-bottom: 20px; }
  &__social-block {
    width: 50%;
    .social {
      margin-left: -7px;
      @include xl {
        margin-left: -5px;
        li {
          margin-left: 5px;
          margin-right: 5px; } }
      @include lg {
        margin-left: 0px;
        li {
          margin-left: 0;
          margin-right: 0; } } } }
  &__social-title {
    margin-bottom: 10px; }
  &__download {
    @include flex(flex-start, center);
    .title {
      margin-right: 10px; }
    .download-sm {
      position: relative;
      top: .2em; } } }

