.form {
  $this: &;
  &__inner {
    // padding-left: 15px
 }    // padding-right: 15px
  &__row {
    margin-right: -15px;
    margin-left: -15px;
    @include flex(flex-start, normal, row, wrap); }
  &__field {
    margin-bottom: 30px;
    position: relative;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    @include md {
      margin-bottom: 15px; }
    @include xs {
      width: 100%; } }
  &__field-50 {
    width: 50%; }
  &__field-center {
    @include flex(center, center); }
  &__btn {}

  &--call {
    // max-width: 330px
    // +xs
    //   max-width: none
    // #{$this}__field
    //   margin-bottom: 40px
    //   text-align: center
    //   +lg
    //     margin-bottom: 30px
    //   +md
    //     margin-bottom: 15px
    //   +sm
    //     margin-bottom: 10px
    // #{$this}__bottom
    //   white-space: nowrap
    //   +lg
 }    //     white-space: normal
  &--subscribe {
    max-width: 330px;
    #{$this}__title {
      margin-bottom: 10px; }
    input {
      border-bottom: 1px solid $gray;
      width: 100%; }
    button {
      position: absolute;
      top: 0;
      right: 0; } } }

.input, .textarea {
  position: relative;
  &.has-focus, &.has-text {
    label {
      &:first-child {
        display: none; } } }

  label {
    &:first-child {
      position: absolute;
      pointer-events: none;
      color: $c-text;
      top: .3em;
      left: 0; } }
  sup {
    display: inline-block;
    vertical-align: top;
    color: red; } }

.input {
  input {
    background-color: transparent;
    padding: 5px 0;
    height: 32px;
    border-bottom: 1px solid  $white;
    width: 100%;
    @include tr(border-color .3s);
    @include placeholder {
      color: $c-text; }
    &:focus {
      border-color: $accent; } }

  &--gray {
    input {
      border-color: $c-text; } } }

.textarea {
  textarea {
    resize: none;
    background-color: transparent;
    height: 32px;
    overflow: hidden;
    width: 100%;
    border: 0;
    border-bottom: 1px solid  $white;
    padding: 5px 0;
    @include tr(border-color .3s);
    @include placeholder {
      color: $c-text; }
    &:focus {
      border-color: $accent; } }
  &--gray {
    textarea {
      border-color: $c-text; } } }

.attach {
  display: block;
  position: relative;
  font-size: $fz-sm;
  cursor: pointer;
  .icon {
    position: relative;
    font-size: 21px;
    margin-right: .3em;
    top: .2em; }
  input {
    visibility: hidden;
    opacity: 0;
    position: absolute; } }

.custom-select {
  $this: &;
  position: relative;
  @include bold;
  select {
    visibility: hidden;
    opacity: 0;
    position: absolute; }
  &.is-open {
    #{$this}__panel {
      visibility: visible;
      opacity: 1; } }
  &__opener {
    border-bottom: 1px solid  $white;
    height: 32px;
    padding: 5px 0;
    position: relative;
    cursor: default;
    @include before {
      @include arr(12, 8, $accent, b);
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      right: 0;
      pointer-events: none; } }
  &__panel {
    background-color: $black;
    position: absolute;
    z-index: 1;
    left: 0;
    top: 100%;
    width: 100%;
    overflow: auto;
    max-height: 250px;
    visibility: hidden;
    opacity: 0;
    @include tr(opacity .3s, visibility .3s); }
  &__option {
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
    @include tr(color .3s, background-color .3s);

    @include hover {
      color: $accent;
      background-color: $white; } } }




