.slider {
  $this: &;
  &__wrap {
    position: relative; }

  &--items {
    &__wrap {
      .tns-ovh {
        padding-top: 20px;
        padding-bottom: 20px; } } } }

.prev,
.next {
  position: relative;
  width: 40px;
  height: 40px;
  color: $black-light;
  @include inline-flex(center, center);
  transform: translate(0, -50%);
  position: absolute;
  top: 50%;
  @include before {
    background-color: $accent;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: skew(15deg);
    position: absolute;
    z-index: 0;
    @include tr(transform .3s); }
  @include after {
    @include icon-arrow(10, 10, 1, currentColor, 45); }
  @include hover {
    @include before {
      transform: skew(-15deg); } } }

.prev {
  left: -100px;
  @include r(1630) {
    left: 0; } }

.next {
  right: -100px;
  @include r(1630) {
    right: 0; }
  @include before {
    transform: skew(-15deg); }
  @include hover {
    @include before {
      transform: skew(15deg); } }
  @include after {
    @include rotate(-135); } }
