.items {
  .item {
    margin-bottom: 30px;
    height: calc(100% - 30px);
    @include xs {
      margin-bottom: 15px;
      height: auto; } } }

.item {
  $this: &;
  background-repeat: no-repeat;
  background-position: 100% 0%;
  background-size: auto;
  position: relative;
  background-color: $white;
  padding: 20px;
  display: block;
  box-shadow: 0px 2px 17px 0px rgba(0, 0, 0, 0.09);
  @include tr(transform .3s);
  height: 100%;
  @include hover {
    @include scale(1.05);
    #{$this}__link {
      color: $black-light;
      .link-arrow__arr {
        width: 80px; } } }
  @include lg {
    background-size: 80%; }
  @include md {
    height: calc(100% - 30px);
    margin-bottom: 30px;
    background-size: 70%; }
  @include sm {
    padding: 10px; }
  @include xs {
    margin-bottom: 15px;
    height: calc(100% - 15px);
    background-size: 50%; }
  &__price {
    // position: absolute
    // top: 20px
    // left: 20px
    display: block; }
  &__content {
    display: block;
    margin-top: 70%;
    @include lg {
      margin-top: 90%; }
    @include md {
      margin-top: 50%; } }
  &__title {
    display: block;
    margin-bottom: 15px; } }



